import { usersAPI } from '../API/API.js'
import { updateObjectInArray } from '../Utils/Helpers/object-helper.js'


const FOLLOW_SUCCESS = 'users/FOLLOW_SUCCESS'
const UNFOLLOW_SUCCESS = 'users/UNFOLLOW_SUCCESS'
const SET_USERS = 'users/SET_USERS'
const SET_CURRENT_PAGE = 'users/SET_CURRENT_PAGE'
const SET_TOTAL_COUNT = 'users/SET_TOTAL_COUNT'
const TOGGLE_IS_FETCHING = 'users/TOGGLE_IS_FETCHING'
const TOGGLE_IS_FOLLOWING_PROGRESS = 'users/TOGGLE_IS_FOLLOWING_PROGRESS'

const initialState = {
  users: [],
  pageSize: 10,
  portionSize:10,
  count: 0,
  currentPage: 1,
  isFetching: false,
  followingInProgress: [],
}

const usersReducer = (state = initialState, action) => {
  switch(action.type) {
    case TOGGLE_IS_FETCHING:
      return {
        ...state,
        isFetching: action.isFetching,
      }
    case TOGGLE_IS_FOLLOWING_PROGRESS:
      return {
        ...state,
        followingInProgress: action.followingInProgress
            ? [ ...state.followingInProgress, action.userID ]
            : [
              state.followingInProgress.filter(id => id !== action.userID) ],
      }
    case SET_USERS:
      return {
        ...state,
        users: [ ...action.users ],
      }
    case SET_CURRENT_PAGE:
      return {
        ...state,
        currentPage: action.currentPage,
      }
    case SET_TOTAL_COUNT:
      return {
        ...state,
        count: action.totalCount,
      }
    case FOLLOW_SUCCESS:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userID, 'id', { followed: true }),
      }
    case UNFOLLOW_SUCCESS:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userID, 'id', { followed: false }),
      }
    default:
      return state
  }
}

//===== Action Creators =========================
export const followSuccess = (userID) => ({
  type: FOLLOW_SUCCESS,
  userID,
})

export const unfollowSuccess = (userID) => ({
  type: UNFOLLOW_SUCCESS,
  userID,
})

export const setUsers = (users) => ({
  type: SET_USERS,
  users,
})

export const setCurrentPage = (currentPage) => ({
  type: SET_CURRENT_PAGE,
  currentPage,
})

export const setTotalUsersCount = (totalCount) => ({
  type: SET_TOTAL_COUNT,
  totalCount,
})

export const toggleIsFetching = (isFetching) => ({
  type: TOGGLE_IS_FETCHING,
  isFetching,
})

export const toggleIsFollowingProgress = (followingInProgress, userID) => ({
  type: TOGGLE_IS_FOLLOWING_PROGRESS,
  followingInProgress,
  userID,
})

//===== Thunks Creators =========================
export const requestUsers = (page, pageSize) => async(dispatch) => {
  dispatch(toggleIsFetching(true))
  let response = await usersAPI.getUsers(page, pageSize)
  dispatch(toggleIsFetching(false))
  dispatch(setUsers(response.data[ 'items' ]))
  dispatch(setTotalUsersCount(response.data[ 'totalCount' ]))
}

const followUnfollow = async(dispatch, id, apiMethod, actionCreator) => {
  dispatch(toggleIsFollowingProgress(true, id))
  let response = await apiMethod(id)
  if(response.data[ 'resultCode' ] === 0) {
    dispatch(actionCreator(id))
  }
  dispatch(toggleIsFollowingProgress(false, id))
}

export const follow = (id) => async(dispatch) => {
  await followUnfollow(dispatch, id, usersAPI.isFollow, followSuccess)
}

export const unfollow = (id) => async(dispatch) => {
  await followUnfollow(dispatch, id, usersAPI.isUnfollow, unfollowSuccess)
}

export default usersReducer