import classes from './Navbar.module.scss'
import { createNavLink } from '../Common/NavLink/NavLink.jsx'
import FrendsContainer from './Frends/FrendsContainer'


const Navbar = (props) => {
  return (<div className = { classes.wrapper }>
    <nav className = { classes.nav }>
      { createNavLink('/profile', 'Profile') }
      { createNavLink('/dialogs', 'Messages') }
      { createNavLink('/gallery', 'Gallery') }
      { createNavLink('/news', 'News') }
      { createNavLink('/music', 'Music') }
      { createNavLink('/users', 'Users') }
      { createNavLink('/settings', 'Settings') }
    </nav>
    <FrendsContainer />
  </div>)
}

export default Navbar
