import classes from './Frends.module.scss'
import Frend from './Frend/Frend'


const Frends = ({ myFrends }) => {
  let frendsElement = myFrends.map (f => (<Frend name = { f.name }
      ava = { f.ava }
      key = { f.id } />))
  return (<div className = { classes.wrapper }>
    <h2 className = { classes.title }>Frends</h2>
    <div className = { classes.content }>{ frendsElement }</div>
  </div>)
}

export default Frends