import { NavLink } from 'react-router-dom'
import classes from './NavLink.module.scss'


 export const createNavLink = (address, nameLink ) => {
  return (
      <NavLink
          to = { address }
          className = { (navData) => navData.isActive
              ? classes.active
              : classes.nav_link }>
        { nameLink }
      </NavLink>
  )
}
