import { imagesAPI } from '../API/API.js'


const SET_IMAGES = 'gallery/SET_IMAGES'

const initialState = {
  
  images: [],
}

const galleryReducer = (state = initialState, action) => {
  switch(action.type) {
    case SET_IMAGES:
      return {
        ...state,
        images: [ ...action.images ],
      }
    default:
      return state
  }
}

//===== Action Creators =========================
export const setImages = (images) => ({
  type: SET_IMAGES,
  images,
})


//===== Thunks Creators =========================

export const requestImages = (currentPageImage, pageSizeImage) => async(dispatch) => {
  let response = await imagesAPI.getImages(currentPageImage, pageSizeImage)
  dispatch(setImages(response.data))
}

export default galleryReducer