const ADD_MESSAGE = 'dialogs/ADD_MESSAGE'

const initialState = {
  dialogs: [
    { id: 1, name: 'Yulia', ava: '' },
    { id: 2, name: 'Marya', ava: '' },
    { id: 3, name: 'Anny', ava: '' },
    { id: 4, name: 'Darya', ava: '' },
    { id: 5, name: 'Pavel', ava: '' },
  ],
  messages: [
    { idMessage: 1, userId: 1, name: 'Yulia', message: 'The yogi syntheses.' },
    {
      idMessage: 2, userId: 2, name: 'Marya', message: 'With truffles drink' +
          ' ketchup.',
    },
    {
      idMessage: 3, userId: 2, name: 'Marya', message: 'Bravely convert a' +
          ' sonic shower.',
    },
    { idMessage: 4, userId: 4, name: 'Darya', message: 'hi' },
    { idMessage: 5, userId: 4, name: 'Darya', message: 'how are you' },
  ],
}

const dialogsReducer = (state = initialState, action) => {
  switch(action.type) {
    case ADD_MESSAGE:
      return {
        ...state,
        messages: [
          ...state.messages, {
            id: 6,
            name: 'Pavel',
            message: action.message,
          } ],
      }
    default:
      return state
  }
}
//===== Action Creators =========================

export const addMessage = (message) => ({
  type: ADD_MESSAGE,
  message,
})

export default dialogsReducer