import classes from './Frend.module.scss'
import { NavLink } from 'react-router-dom'
import photoFrend from '../../../../assets/image/UserFemale.png'


const Frend = (props) => {
  return (<div className = { classes.wrapper }>
    <NavLink to = '*\*'
        className = { classes.frend }>
      <span>{ props.name }</span>
      <img src = { props.ava
          ? props.ava
          : photoFrend }
          alt = 'ava' />
    </NavLink>
  </div>)
}

export default Frend