import { connect } from 'react-redux'
import { useEffect, useState } from 'react'
import { requestImages } from '../../Redux/gallery-reducer.js'
import classes from './MyGallery.module.scss'
import 'react-responsive-carousel/lib/styles/carousel.min.css' // requires a
                                                                // loader
import { Carousel } from 'react-responsive-carousel'


const MyGallery = ({ images, getImages, }) => {
  
  let [pageLoading,setLoading]=useState(false)
  useEffect(() => {
    getImages(1, 15)
    setLoading(true)
  },[pageLoading])
  
  let carouselElement = images.map(i => <div>
    <img src = { i.download_url } />
    {/*<p className = 'legend'>Legend </p>*/}
  </div>)
  
  return (
      <div className = { classes.gallery }>
        <MyCarousel carouselElement = { carouselElement } />
      </div>
  )
}
const MyCarousel = ({ carouselElement }) => {
  return (
      <Carousel dynamicHeight = { true }>
        { carouselElement }
      </Carousel>
  )
}

let mapStateToProps = (state) => ({
  images: state.gallery.images,
})
export default connect(mapStateToProps, { getImages: requestImages })(MyGallery)