import Frends from './Frends'
import { connect } from 'react-redux'


let mapStateToProps = (state) => {
  return {
    myFrends: state.navbar.myFrends,
  }
}
let mapDispatchToProps = () => {
  return {
    error: 0,
  }
}
export default connect (mapStateToProps, mapDispatchToProps) (Frends)

 