import axios from 'axios'


const instance = axios.create({
  baseURL: 'https://social-network.samuraijs.com/api/1.0/',
  withCredentials: true,
  headers: {
    'API-KEY': 'bbf0c796-aa11-49da-bf5a-d730e5d3d684',
  },
})
const instanceImage = axios.create({
  baseURL: 'https://picsum.photos/v2/list',
  // withCredentials: true,
  // headers: {
  //   'API-KEY': 'bbf0c796-aa11-49da-bf5a-d730e5d3d684',
  // },
})

export const usersAPI = {
  getUsers(currentPage = 1, pageSize = 5) {
    return (instance.get(`users?page=${ currentPage }&count=${ pageSize }`))
  },
  isFollow(id) {
    return (instance.post(`follow/${ id }`))
  },
  isUnfollow(id) {
    return (instance.delete(`follow/${ id }`))
  },
}

export const profileAPI = {
  getProfile(userId = '2256') {
    return (instance.get(`profile/${ userId }`))
  },
  getStatus(userId = '2256') {
    return (instance.get(`profile/status/${ userId }`))
  },
  updateStatus(status) {
    return (instance.put(`profile/status`, { status }))
  },
  updateProfile(profile) {
    return (instance.put(`profile`, profile))
  },
  savePhoto(photoFile) {
    const formData = new FormData()
    formData.append('image', photoFile)
    return (instance.post('profile/photo', formData,
        { headers: { 'Content-Type': 'multipart/form-data' } }))
  },
  
}

export const authAPI = {
  me() {
    return (instance.get(`auth/me`))
  },
  logIn(email, password, rememberMe = false, captcha = null) {
    return (instance.post(`auth/login`, {
      email,
      password,
      rememberMe,
      captcha,
    }))
  },
  logOut() {
    return (instance.delete(`auth/login`))
  },
  
}
export const securityAPI = {
  getCaptchaUrl() {
    return (instance.get(`security/get-captcha-url`))
  },
}
export const  imagesAPI = {
  getImages(currentPageImage = 1, pageSizeImage = 10) {
    return (instanceImage.get(`?page=${ currentPageImage }&limit=${ pageSizeImage }`))
  },}