import classes from './App.module.scss'
import { HashRouter, Navigate, Route, Routes } from 'react-router-dom'
import Navbar from '../Navbar/Navbar'
import News from '../News/News'
import Music from '../Music/Music'
import Settings from '../Settings/Settings'
import HeaderContainer from '../Header/HeaderContainer'
import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { compose } from 'redux'
import { initializeApp } from '../../Redux/app-reducer.js'
import Preloader from '../Common/Preloader/Preloader.jsx'
import { withSuspense } from '../../HOC/WithSuspense.js'
import MyGallery from '../Gallery/MyGallery.jsx'


const ProfileContainer = React.lazy(() => import('../Profile/ProfileContainer.jsx'))
const DialogsContainer = React.lazy(() => import('../Dialogs/DialogsContainer.jsx'))
const UsersContainer = React.lazy(() => import('../Users/UsersContainer.jsx'))
const LoginPage = React.lazy(() => import('../Login/LoginPage.jsx'))

const App = ({ initialized, initializeApp }) => {
  
  const [ initialize, setInitialise ] = useState(false)
  
  useEffect(() => {
    initializeApp()
    setInitialise(initialized)
  }, [ initialized ])
  
  if(!initialize) {
    return <Preloader />
  }
  return (<HashRouter>   {/* for GitHub pages*/ }
    {/*<BrowserRouter>*/ }
    <div className = { classes.wrapper }>
      <HeaderContainer />
      <Navbar />
      <div className = { classes.content }>
        <Routes>
          <Route
              path = '/'
              element = {
                <Navigate to = '/profile/*' />
              } />
          <Route
              path = '/login'
              element = { withSuspense(LoginPage) } />
          <Route
              path = '/profile/*'
              element = { withSuspense(ProfileContainer) } />
          <Route
              path = '/dialogs/*'
              element = { withSuspense(DialogsContainer) } />
          <Route
              path = '/gallery/*'
              element = { <MyGallery /> } />
          <Route
              path = '/news/*'
              element = { <News /> } />
          <Route
              path = '/music/*'
              element = { <Music /> } />
          <Route
              path = '/settings/*'
              element = { <Settings /> } />
          <Route
              path = '/users/*'
              element = { withSuspense(UsersContainer) } />
          <Route
              path = '*'
              element = { <div>404 not found</div> } />
        </Routes>
      </div>
    </div>
    {/*</BrowserRouter>*/ }
  </HashRouter>)
}

const mapStateToProps = (state) => ({
  initialized: state.app.initialized,
})
export default compose(connect(mapStateToProps, { initializeApp }))(App)