import { authAPI, securityAPI } from '../API/API.js'
import { stopSubmit } from 'redux-form'
import { getUserProfile } from './profile-reducer.js'


const SET_USER_DATA = 'auth/SET_USER_DATA'
const SET_IS_AUTH = 'auth/SET_IS_AUTH'
const GET_CAPTCHA_URL_SUCCESS = 'auth/SET_CAPTCHA_URL_SUCCESS'

const initialState = {
  userId: null,
  email: null,
  login: null,
  isAuth: false,
  captchaUrl: null,
}

const authReducer = (state = initialState, action) => {
  switch(action.type) {
    case SET_USER_DATA:
    case GET_CAPTCHA_URL_SUCCESS:
      return {
        ...state, ...action.payload,
      }
    case SET_IS_AUTH:
      return {
        ...state,
        isAuth: action.value,
      }
    default:
      return state
  }
}

//===== Action Creators =========================

export const setAuthUserData = (userId, email, login) => ({
  type: SET_USER_DATA,
  payload: {
    userId,
    email,
    login,
  },
})
export const setIsAuthUser = (value) => ({
  type: SET_IS_AUTH,
  value,
})
export const getCaptchaUrlSuccess = (captchaUrl) => ({
  type: GET_CAPTCHA_URL_SUCCESS,
  payload: {
    captchaUrl,
  },
})

//===== Thunks Creators =========================
export const getAuthUserData = () => async(dispatch) => {
  let response = await authAPI.me()
  if(response.data[ 'resultCode' ] === 0) {
    let { id, email, login } = response.data.data
    dispatch(getUserProfile(id))
    dispatch(setIsAuthUser(true))
    dispatch(setAuthUserData(id, email, login))
  }
}

export const Login = (email, password, rememberMe, captcha) => async(dispatch) => {
  let response = await authAPI.logIn(email, password, rememberMe, captcha)
  if(response.data[ 'resultCode' ] === 0) {
    dispatch(getAuthUserData(true))
    dispatch(getCaptchaUrlSuccess(null))
  } else {
    if(response.data[ 'resultCode' ] === 10) {
      dispatch(getCaptchaUrl())
    } else {
      let message = response.data.messages.length > 0
          ? response.data.messages[ 0 ]
          : 'Common error'
      dispatch(stopSubmit('Login', { _error: message }))
    }
  }
}

export const Logout = () => async(dispatch) => {
  let response = await authAPI.logOut()
  if(response.data[ 'resultCode' ] === 0) {
    dispatch(setAuthUserData(null, null, null))
    dispatch(setIsAuthUser(false))
    dispatch(getCaptchaUrlSuccess(null))
  }
}

const getCaptchaUrl = () => async(dispatch) => {
  let response = await securityAPI.getCaptchaUrl()
  const captchaUrl = response.data.url
  dispatch(getCaptchaUrlSuccess(captchaUrl))
  console.log(captchaUrl)
}

export default authReducer