import { profileAPI } from '../API/API.js'
import { stopSubmit } from 'redux-form'


const ADD_POST = 'profile/ADD_POST'
const DELETE_POST = 'profile/DELETE_POST'
const SET_USER_PROFILE = 'profile/SET_USER_PROFILE'
const SET_USER_STATUS = 'profile/SET_USER_STATUS'
const SET_PHOTO_SUCCESS = 'profile/SET_PHOTO_SUCCESS'
const SET_EDIT_MOD = 'profile/SET_EDIT_MOD'

const initialState = {
  profile: null,
  editMod: false,
  status: '',
  posts: [
    { id: 1, text: 'Hello', likeCount: 10 },
    { id: 2, text: 'Hi, how are you?', likeCount: 15 },
    { id: 3, text: 'It\'s my first post.', likeCount: 5 },
    { id: 4, text: 'Yo ho ho.', likeCount: 5 } ],
}

const profileReducer = (state = initialState, action) => {
  switch(action.type) {
    case SET_USER_PROFILE: {
      return { ...state, profile: action.profile }
    }
    case SET_USER_STATUS: {
      return { ...state, status: action.status }
    }
    case ADD_POST:
      return {
        ...state,
        posts: [
          ...state.posts, { id: 5, text: action.newPostText, likeCount: 0 } ],
      }
    case DELETE_POST:
      return {
        ...state,
        posts: state.posts.filter(p => p.id !== action.postId),
      }
    case SET_PHOTO_SUCCESS:
      return {
        ...state, profile: { ...state.profile, photos: action.photos },
      }
    case SET_EDIT_MOD:
      return {
        ...state, editMod: action.editMod,
      }
    default:
      return state
  }
}

//===== Action Creators =========================
export const addPost = (newPostText) => ({
  type: ADD_POST,
  newPostText,
})

export const deletePost = (postId) => ({
  type: DELETE_POST,
  postId,
})

export const setUserStatus = (status) => ({
  type: SET_USER_STATUS,
  status,
})
export const setUserProfile = (profile) => ({
  type: SET_USER_PROFILE,
  profile,
})
export const savePhotoSuccess = (photos) => ({
  type: SET_PHOTO_SUCCESS,
  photos,
})
export const setEditMod = (editMod) => ({
  type: SET_EDIT_MOD,
  editMod,
})

//===== Thunks Creators =========================
export const getUserProfile = (userId) => async(dispatch) => {
  const response = await profileAPI.getProfile(userId)
  dispatch(setUserProfile(response.data))
}

export const getUserStatus = (userId) => async(dispatch) => {
  const response = await profileAPI.getStatus(userId)
  dispatch(setUserStatus(response.data))
}

export const updateUserStatus = (status) => async(dispatch) => {
  try {
    const response = await profileAPI.updateStatus(status)
    if(response.data.resultCode === 0) {
      dispatch(setUserStatus(status))
    }
  } catch(error) {
    alert('error')
  }
}
export const saveProfile = (profile) => async(dispatch, getState) => {
  const userId = getState().auth.userId
  const response = await profileAPI.updateProfile(profile)
  if(response.data.resultCode === 0) {
    dispatch(getUserProfile(userId))
    dispatch(setEditMod(false))
  } else {
    // масссив содержащий строку с ошибками
    let message = response.data.messages.length > 0
        ? response.data.messages
        : 'Common error'
    // создание объекта с ошибкой для отображения в конкретной строке
    for(let element of message) {
      const errorElements = element.split(/[()>-]/)
      // { 'contacts': { 'facebook': response.data.messages[ 0 ] } }
      const errorElement =
          (errorElements.length < 4)
              ? { [ errorElements[ 1 ][ 0 ].toLowerCase() + errorElements[ 1 ].slice(1) ]: [ errorElements[ 0 ].toString() ] }
              : {
                [ errorElements[ 1 ].toLowerCase() ]: {
                  [ errorElements[ 3 ].toLowerCase() ]: [ errorElements[ 0 ] ],
                },
              }
      
      dispatch(stopSubmit('Profile', errorElement))
    }
    dispatch(setEditMod(true))
  }
}
export const updatePhoto = (photoFile) => async(dispatch) => {
  const response = await profileAPI.savePhoto(photoFile)
  if(response.data.resultCode === 0) {
    dispatch(savePhotoSuccess(response.data.data.photos))
  }
}
export default profileReducer